import homePage from "./home/homePage";
import babyClothes from "./home/babyClothes";
import newProducts from "./home/newProducts";
import realShooting from "./home/realShooting";
import buyShop from "./home/buyShop";
import member from "./home/member";
import stickySlot from '../components/stickySlot/StickySlot'
// import TestNav from "../components/testNav";
// import Headers from '../components/home/headers'
// import Dao from '../components/Dao'
export default {
  name: "Index",
  components: {
      stickySlot,
      homePage,
      babyClothes,
      newProducts,
      realShooting,
      buyShop,
      member
    // TestNav,
    // Dao,
    // Headers,
  },
  data () {
    return {
      follow:0,
      sheetVisible: false,
      headInfo: {
        shopName: '蜗牛工作室',
        img: require("../assets/imghead.jpg"),
        number: '973'
      },
      ReputationLevel: [
        require("../assets/huangguan.png"),
        require("../assets/huangguan.png"),
        require("../assets/huangguan.png"),
        require("../assets/huangguan.png"),
        require("../assets/huangguan.png"),
      ],
      actions: [],
      tabItems: [
        {status: "首页", id: 0},
        {status: "宝贝", id: 1},
        {status: "新品", id: 2},
        {status: "实拍", id: 3},
        {status: "买家秀", id: 4},
        {status: "会员", id: 5}
      ],
      tabContent: [
        {path: 'homePage'},
        {path: 'babyClothes'},
        {path: 'newProducts'},
        {path:'realShooting'},
        {path: 'buyShop'},
        {path: 'member'}
      ],
      index01: 0,
      index: 0,
      /*刷新*/
      noDate: false, //判断是否加载的开关
      arr:[],
      page:1,
      pageSize:10

    }
  },
  methods: {
    // 刷新
    // getData () {
    //   var vm = this;
    //
    // },
    // 下拉刷新
    refresh () {
      console.log('refresh')
      // this.timeout = setTimeout(() => {
      //   this.$refs.myscroller.finishPullToRefresh()
      // },1500)
    },
    // 上拉加载
    infinite () {
      console.log('infinite')
      // this.timeout = setTimeout(() => {
      //   this.$refs.myscroller.finishPullToRefresh()
      // },1500)
    },
    //
      guanzhu () {
        this.$vux.toast.show({
            text: '关注成功！',
        })
        this.follow = 1
      },
      openAlbum() {
        console.log('容朕想想');
        this.$vux.toast.show({
            text: '谢谢！！！'
        })
      },
      takePhoto() {
        console.log(this.sheetVisible);
        this.follow = 0
        if (this.follow){
          this.sheetVisible = false
          this.$refs.btn1.style.innerText =  '关注'
        } else {
          this.sheetVisible = true
          this.$refs.btn1.style.innerText =  '已关注'
        }
      },
      more () {
        this.$vux.toast.show({
          text: '没有此功能的啦！！！',
          type: 'cancel'
        })
      },
      btnClose () {
        const _this = this
        _this.$vux.confirm.show({
          title: '确定退出吗？',
          onConfirm() {
              this.$router.go(-1);
          },
          onCancel() {
              console.log(_this)
          }
        })
      },
      switchTabItem (index) {
        console.log('on-before-index-change', index)
        // if(index === 0) {
        //   this.$router.push({
        //     path: 'scroll'
        //   })
        // }
        this.$vux.loading.show({
          text: 'loading'
        })
        setTimeout(() => {
          this.$vux.loading.hide()
          this.index01 = index
        }, 300)
      },
    },
  mounted() {
    this.actions = [{
      name: '取消关注',
      method: this.takePhoto
    }, {
        name: '容朕想想',
        method: this.openAlbum
      }
    ];
  }
}



